body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.topBar {
  background: #3a3a3a;
  height: 70px;
  position: relative; }
  .topBar .innerBarWrapper {
    position: relative;
    width: 100%;
    height: 100%; }
    .topBar .innerBarWrapper .innerBar {
      top: 50%;
      position: absolute;
      -webkit-transform: translate(0, -50%);
              transform: translate(0, -50%);
      padding-left: 20px;
      padding-right: 20px;
      width: calc(100% - 40px);
      display: flex;
      flex-wrap: nowrap;
      flex-direction: row; }
      .topBar .innerBarWrapper .innerBar .topBarBlock {
        order: 1; }
        .topBar .innerBarWrapper .innerBar .topBarBlock .bslogo {
          width: 100px; }
        .topBar .innerBarWrapper .innerBar .topBarBlock .topLinkWrapper {
          padding-left: 20px;
          padding-top: 10px;
          height: 40px;
          display: flex;
          justify-content: center;
          align-content: center;
          flex-direction: column; }
          .topBar .innerBarWrapper .innerBar .topBarBlock .topLinkWrapper .topLink {
            bottom: 0;
            font-size: 17px;
            text-decoration: none;
            color: #f6f7f9;
            line-height: 20px; }
      .topBar .innerBarWrapper .innerBar .linkLonger {
        width: 200px !important; }
        .topBar .innerBarWrapper .innerBar .linkLonger .topLinkWrapper {
          width: 200px !important; }

button {
  font: inherit;
  width: 150px;
  height: 40px;
  border-radius: 30px;
  color: #3a3a3a;
  font-weight: bold;
  background: white;
  box-shadow: none;
  font-size: 15px;
  border: none;
  box-shadow: 2px 10px 34px -10px rgba(0, 0, 0, 0.75); }

div #comparison {
  margin: 20px; }

#comparison {
  display: flex;
  flex-direction: column; }
  #comparison #top {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center; }
    #comparison #top img {
      margin: 60px; }
  #comparison #down {
    display: flex;
    align-items: center;
    justify-content: center; }

#landing {
  height: 100vh;
  display: flex;
  flex-direction: row; }
  #landing #left {
    margin-top: auto;
    margin-bottom: auto;
    margin-left: 10%;
    margin-right: 5%;
    align-items: center;
    justify-content: center; }
    #landing #left h1 {
      color: #c51e2b;
      padding: 30px; }
    #landing #left p {
      padding: 30px; }
    #landing #left #btn {
      padding: 30px; }
  #landing #right img {
