.featureScroll {
  padding: 30px; }
  .featureScroll .featureWrapper {
    margin: 0 auto;
    display: flex;
    width: calc(60vw + 200px); }
    .featureScroll .featureWrapper .screenshot {
      box-shadow: 0px 0px 15px -10px #737373;
      display: inline-block;
      width: 60vw; }
      .featureScroll .featureWrapper .screenshot img {
        width: 100%;
        height: 100%; }
    .featureScroll .featureWrapper .featureList {
      padding: 20px; }
      .featureScroll .featureWrapper .featureList .features .featureCard {
        margin-bottom: 5px;
        width: 200px;
        height: 80px;
        display: flex;
        padding: 15px;
        box-sizing: border-box;
        align-items: center;
        line-height: 20px;
        -webkit-transition: box-shadow .2s ease-out 0s;
        transition: box-shadow .2s ease-out 0s;
        box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.75);
        border-radius: 10px;
        background-color: white;
        border: none;
        outline: none; }
        .featureScroll .featureWrapper .featureList .features .featureCard .icon {
          height: 40px;
          width: 40px; }
          .featureScroll .featureWrapper .featureList .features .featureCard .icon img {
            -webkit-filter: grayscale(100%);
            filter: grayscale(100%);
            width: 100%;
            height: 100%; }
        .featureScroll .featureWrapper .featureList .features .featureCard .text {
          text-align: left;
          color: #858585;
          margin-left: 10px;
          width: calc(100% - 50px); }
        .featureScroll .featureWrapper .featureList .features .featureCard:hover {
          cursor: pointer; }
      .featureScroll .featureWrapper .featureList .features .featured {
        box-shadow: 0px 0px 15px -10px #737373;
        -webkit-transition: box-shadow .2s ease-out .2s;
        transition: box-shadow .2s ease-out .2s; }
        .featureScroll .featureWrapper .featureList .features .featured .icon img {
          -webkit-filter: none;
          filter: none; }
        .featureScroll .featureWrapper .featureList .features .featured .text {
          color: black; }
      .featureScroll .featureWrapper .featureList .more {
        margin-bottom: 5px;
        width: 200px;
        height: 80px;
        display: flex;
        padding: 15px;
        box-sizing: border-box;
        align-items: center;
        line-height: 20px;
        border-radius: 10px; }
        .featureScroll .featureWrapper .featureList .more a {
          margin: 0 auto; }

